import React from "react";
import {addToCart, setSelectedVariantOnRecommended} from "eshop/modules/simfree/actions/app";
import {OraButton} from "eshop/components/OraCommonComponents";
import ProductRecomendationOneProduct from "eshop/modules/simfree/components/ProductRecomendationOneProduct";

class ProductRecomendationView extends React.Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.loadModules();
    }

    componentWillUpdate(){
        this.killModules();
    }

    componentDidUpdate(){
        this.loadModules();
    }

    render() {
        if (!this.props.products || this.props.products == null || this.props.products.length === 0) {
            return null;
        }
        return (
            <div id={"product-recommended-anchor" + this.props.productId} className="js-expander__container opl-wiking-expander__container">
                <p data-skiplinks={this.props.label}
                   className="h2 u-padding-l u-small-no-padding-b u-small-padding-left-xl u-no-spacing">{this.props.label}</p>
                <a href="#" className="js-expander__trigger opl-wiking-expander__trigger">
                    <span className="g-icon g-icon--only g-icon--arrow-down-slim"></span>
                    <span className="u-acc-hide">rozwi≈Ñ</span>
                </a>
                <div className="js-expander__content opl-wiking-expander__content">
                    <div className="u-small-padding-top-l u-spacing-xl u-small-no-spacing-bottom">
                        <div ref={ref=> this.ref = ref} className="l-row opl-carousel opl-wiking-gallery-single u-padding-left u-padding-right">
                            {this.props.products.map((phone, index) => {
                                return <ProductRecomendationOneProduct phone={phone} index={index} key={"recomendation"+index}
                                                                       products={this.props.products}/>
                            })}
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    killModules(){
        let element = document.getElementById("recommendation-carousel");
        if(element) {
            OPL.UI.stopModules(element);
        }
    }

    loadModules() {
        if (this.ref) {
            OPL.UI.loadModules(this.ref, [
                {
                    path: "core/modules/layout-fixer",
                    conditions: "element:{seen}",
                    options: {
                        selectors: [
                            ".js-layout-fixer-group-1",
                            ".js-layout-fixer-group-2"
                        ]
                    }
                }, {
                    path: "common/modules/opl-carousel",
                    conditions: "element:{was seen}",
                    options: {
                        navAdditionalClass: "o-btn-arrow--rounded",
                        slide: ".opl-carousel__recomended-item",
                        slidesToShow: 4,
                        autoplay: false,
                        arrows: false,
                        dots: true,
                        responsive: [{
                            breakpoint: 767,
                            settings: {
                                slidesToShow: 1,
                                dots: true
                            }
                        }, {
                            breakpoint: 1200,
                            settings: {
                                slidesToShow: 3
                            }
                        }]
                    }
                }, {
                    path: "common/modules/opl-width-fixer"
                }
            ]);
        }
    }
}

export default ProductRecomendationView;